import React from 'react'
import { ListGroup, Button } from 'react-bootstrap'
import { useAppDispatch } from '../hooks/redux'
import { setOrderStatus } from '../store/Thunk/orderThunk'

interface OrderPageHeaderProps {
 id: string,
 status: number,
 infoOrderLength: number,
 done: number
}

const OrderPageHeader = React.memo(({ id, infoOrderLength, status, done }: OrderPageHeaderProps) => {
 const dispatch = useAppDispatch()

 return (
  <ListGroup horizontal={'xxl'} className="my-2">
   <ListGroup.Item className='d-flex justify-content-center align-items-center flex-grow-1' active> : {String(id).length > 2 ? Number(String(id).slice(-2)) : id}</ListGroup.Item>

   <ListGroup.Item className='d-flex justify-content-center align-items-center flex-grow-1' variant='success'>
    : {status === 1 ? '' : status === 2 ? '' : ''}
   </ListGroup.Item>

   <ListGroup.Item className='d-flex justify-content-center align-items-center flex-grow-1' variant={status === 2 ? 'success' : done === infoOrderLength ? 'success' : 'danger'}>
    : {status === 1 ? done : infoOrderLength}  {infoOrderLength}
   </ListGroup.Item>

   {done === infoOrderLength && status === 1 && <ListGroup.Item className='flex-grow-1'>
    <Button variant="success" onClick={() => dispatch(setOrderStatus({ id: Number(id), status: 2 }))}></Button>
   </ListGroup.Item>}

   {status === 2 && <ListGroup.Item className='flex-grow-1'>
    <Button variant="success" onClick={() => dispatch(setOrderStatus({ id: Number(id), status: 3 }))}></Button>
   </ListGroup.Item>}

  </ListGroup>
 )
})

export default OrderPageHeader